<template>
  <div>
    <go-back></go-back>
      <h3>{{detail.name}}</h3>
      <p v-if="detail.price">价格：{{detail.price|filterPrice}}</p>
      <hr>
      <h3>猜你喜欢</h3>
    <ul>
      <li class="box" v-for="item in likeList" :key="item.foodId"
     is="router-link"
     :to="'/foodDetail/'+item.foodId"
      >
        <h3>{{item.name}}</h3>

      </li>
    </ul>
  </div>
</template>

<script>
import {reqFoodDetail,reqFood} from "../../utils/http"
export default {
  data(){
    return {
      detail:{},
      likeList:[]
    }
  },
  mounted(){
    let id=this.$route.params.foodId;
    console.log(id)
    //请求详情
    this.getDetail(id)

    //猜你喜欢列表
    reqFood().then(res=>{
      console.log(res)
      this.likeList=res.data.data
    })
  },
  methods:{
    getDetail(id){
     reqFoodDetail(id).then(res=>{
       console.log(res)
        this.detail=res.data.detail

      })
    }
  },
  beforeRouteUpdate(to,from,next){
    this.getDetail(to.params.foodId)
    next()
  }
}
</script>

<style scoped>
a{
  display: block;
}
</style>



